const ADD_MESSAGE = "ADD-MESSAGE";


let initialState = {
    dialogsData: [
        {
            id: 1,
            name: "Leonid",
            avatar: "https://whatsism.com/uploads/posts/2018-07/1530546770_rmk_vdjbx10.jpg",
            messageData: [
                {author: "author:", text: "Hello, how are?"},

            ]


        },
        {
            id: 2,
            name: "Dima",
            text: "Hi",
            avatar: "data:image/jpeg;base64,"
            ,
            messageData: [
                {author: "author:  ", text: "Hey!"},

            ],
        },
        {
            id: 3,
            name: "Nastya",
            avatar: "https://i.pinimg.com/originals/fc/2b/17/fc2b1740c218d870aa9ec0be0760d93e.jpg",
            messageData: [
                {author: "author:  ", text: "Good morning!"},

            ],


        },
        {
            id: 4,
            name: "Sasha",
            avatar: "https://proprikol.ru/wp-content/uploads/2020/02/kartinki-na-avatarku-dlya-parnej-i-muzhchin-1-1.jpg",
            messageData: [
                {author: "author:  ", text: "Privet!"}, {author: "author:", text: "Kak dela?"}

            ],


        },
        {
            id: 5,
            name: "Papich",
            avatar: "data:image/jpeg;base64,"
            ,
            messageData: [
                {author: "author:  ", text: "Zdarova"},

            ],

        }],


};


const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_MESSAGE : {
            let stateCopy = {...state}
            let newMessage = {author: "you:", text: action.newMessageText};
            let index = window.location.href.slice(-1);


            stateCopy = {...state, dialogsData: [...state.dialogsData.map(obj => ({...obj}))],}

            for (let i = 0; i < state.dialogsData.length; i++) {
                stateCopy.dialogsData[i].messageData = [...state.dialogsData[i].messageData]
            }

            stateCopy.dialogsData[index - 1].messageData.push(newMessage);
            stateCopy.newMessageText = "";
            return stateCopy

        }


        default : {
            return state
        }

    }


}



export const addMessage = (newMessageText) => ({type: ADD_MESSAGE, newMessageText})

export default dialogsReducer
